/*=============================================================================
# LAYOUT
==============================================================================*/
.content-block {
  max-width: 55rem; }

#main-content {
  margin: 0 auto;
  margin-top: 10rem;
  margin-top: 17vh;
  max-width: 60%;
  position: relative; }

/*=============================================================================
# NAVIGATION
==============================================================================*/
#socialmedia {
  position: fixed;
  padding: 10px;
  right: 38px;
  top: 38px;
  color: rgba(255, 255, 255, 0.3);
  transition: color .3s;
  display: flex; }
  #socialmedia span {
    line-height: 24px;
    display: block;
    height: 24px; }
  #socialmedia a {
    margin-left: 20px;
    display: block; }
  #socialmedia svg {
    fill: rgba(255, 255, 255, 0.3);
    width: 24px;
    height: auto;
    transition: fill .3s; }
    #socialmedia svg:hover {
      fill: rgba(255, 255, 255, 0.8); }
  #socialmedia:hover {
    opacity: 1; }

/*=============================================================================
# LANDING PAGE
==============================================================================*/
.landing-details {
  display: flex;
  margin-top: 5rem;
  height: 70px; }
  .landing-details .event-details-lg {
    font-weight: 500;
    font-size: 1.125rem;
    letter-spacing: 2px;
    display: block;
    margin: 13px 0 2px; }
  .landing-details .event-details-sm {
    opacity: .7;
    display: block; }
  .landing-details .detailbox {
    display: flex;
    margin-right: 60px; }
    .landing-details .detailbox img {
      width: 60px;
      display: block;
      margin-right: 20px;
      opacity: .9; }
    .landing-details .detailbox.fam img {
      width: 68px; }
    .landing-details .detailbox.time img {
      margin-top: 7px; }

.horizon-glyph-glyph {
  background-image: url(../images/H.svg);
  background-size: contain;
  background-position: center; }

.horizon-glyph {
  width: 1.75rem;
  height: 1.5rem;
  overflow: hidden;
  position: relative;
  display: inline-block; }
  .horizon-glyph .horizon-glyph-glyph {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: transform 1s;
    transform: translateY(100%); }
  .horizon-glyph.toast-is-ready .horizon-glyph-glyph {
    animation: glyphToaster 2s forwards;
    animation-iteration-count: 1; }

@keyframes glyphToaster {
  from {
    transform: translateY(100%); }
  to {
    transform: translateY(0%); } }

/*=============================================================================
# HOMEPAGE POLYS
==============================================================================*/
.poly-background {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  overflow: hidden; }
  .poly-background svg {
    position: relative;
    height: auto;
    fill: none;
    stroke-linecap: square;
    stroke-miterlimit: 10;
    opacity: .8;
    animation-duration: 15s;
    animation-timing-function: linear;
    animation-iteration-count: infinite; }
  .poly-background svg:nth-of-type(1) {
    top: 12%;
    left: -4%;
    width: 885px;
    animation-name: svg1ColorCycle; }
    .poly-background svg:nth-of-type(1) .b {
      opacity: .25; }
  .poly-background svg:nth-of-type(2) {
    top: 7%;
    left: 29%;
    width: 730px;
    animation-name: svg2ColorCycle; }
    .poly-background svg:nth-of-type(2) .b {
      opacity: .25; }
  .poly-background svg:nth-of-type(3) {
    top: 53%;
    left: -28%;
    width: 401px;
    animation-name: svg3ColorCycle; }
    .poly-background svg:nth-of-type(3) .b {
      opacity: .25; }

@keyframes svg1ColorCycle {
  0% {
    stroke: #ff0080; }
  25% {
    stroke: yellow; }
  50% {
    stroke: #00ff80; }
  75% {
    stroke: blue; }
  100% {
    stroke: #ff0080; } }

@keyframes svg2ColorCycle {
  0% {
    stroke: #80ff00; }
  25% {
    stroke: cyan; }
  50% {
    stroke: #8000ff; }
  75% {
    stroke: red; }
  100% {
    stroke: #80ff00; } }

@keyframes svg3ColorCycle {
  0% {
    stroke: #0080ff; }
  25% {
    stroke: magenta; }
  50% {
    stroke: #ff8000; }
  75% {
    stroke: lime; }
  100% {
    stroke: #0080ff; } }
